.App {
  font-family: sans-serif;
  text-align: center;
  height: 95vh;
  /* width: 100% !important; */
  width: 99vw !important;
  /* border: 3px blue solid; */
}

.App * {
  color: white;
  margin: 0;
  padding: 0;
}
.df {
  display: flex;
  width: 100%;
}

.custom_message .df button {
  width: 50% !important;
}

.custom_message .df button:nth-child(1) {
  /* margin-right: 5px; */
  border-right: 1px grey solid !important;
}

body {
  background-color: #18181b;
  display: flex;
  justify-content: center;
  align-items: center;
  /* width: 100%; */
  /* border: 3px red solid; */
}

button {
  background-color: black;
  color: white;
  margin: 5px;
  padding: 5px;
}

input,
textarea {
  background-color: #464649;
}

.random_messages {
  /* max-width: 320px; */
  /* border: 1px white solid; */
  width: 350px;
}

.custom_message {
  display: flex;
  margin-top: 10px;
  width: 350px;
  /* border: 1px white solid; */

  /* width: 100%; */
}

.custom_message button {
  height: 50px;
  font-size: 1rem;
}

.custom_message .users {
  display: flex;
  flex-direction: column;
  height: 250px;
  width: 300px;
  /* overflow-x: scroll; */
  overflow-x: hidden;
  /* overflow-y: hidden; */
}

.custom_message .users button {
  height: 30px;
  margin: 5px;
}

.custom_message .left button {
  border: 1px black solid;
  /* outline: none; */
  width: 180px;
}
.edit_component button {
  margin: 10px;
  height: 50px;
  width: 150px;
  font-size: 1rem;
}
.custom_message textarea {
  min-height: 205px;
  /* margin-right: 5px; */
  width: 180px;
}

.embed_player {
  margin: 5px;
  /* width: 1440px; */

  width: 100% !important;
  height: 100%;
  grid-area: player;
}
.embed_chat {
  grid-area: chat;
  margin: 5px;
  height: 100%;
  z-index: 999;
}

.dirty {
  grid-area: dirty;
  margin: 5px;
  height: 50%;
  /* margin-right: -5px; */
  justify-content: space-between;
  display: flex;
  flex-direction: column;
  /* width: 30% !important; */
  /* overflow: hidden; */
  /* overflow-y: scroll; */
  /* overflow-y:  scroll hidden; */
}

.wrapper {
  display: flex;
  /* width: 100vh; */
  /* border: 5px green solid; */
  height: 100%;
}

.right {
  height: 100%;
  display: flex;
  /* flex-direction: column; */
  flex-direction: column-reverse;
  /* border: 1px grey solid; */
  justify-content: space-between;
  /* width: 20%; */
}

.random_messages .row {
  /* display: grid; */
  /* grid-template-columns: 1fr 1fr 1fr 1fr 1fr; */
  display: flex;
  justify-content: space-between !important;
  width: 100%;
  margin: 0;
  padding: 0;
  /* border: 1px grey solid; */
  /* width: 357px; */
}

.random_messages button {
  background-size: 100%;
  height: 50px;
  width: 50px;
  /* min-width: 50px; */
  /* background: none; */
  /* background-image: none; */
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.list {
  overflow: scroll;
  height: 200px;
}
.stream-chat-header {
  display: none !important;
}

.connect_users {
  width: 190px;
}

.text-areas textarea {
  margin: 10px 0;
}

.text-areas textarea:nth-child(3) {
  min-height: 100px;
}

.text-areas * {
  color: white !important;
}

.text-areas {
  display: flex;
  flex-direction: column;
  width: 80%;
  max-width: 300px;
  margin: 0 auto;
}

textarea::placeholder {
  color: white !important;
}

.wrapper_login {
  height: 99vh;
  width: 99vw;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wrapper_login .login button {
  background-color: #9147ff;
  font-size: 1.2em;
  padding: 10px;
  margin: 5px 0;
  border-radius: 5px;
  border: 1px black solid;
}

.wrapper_login .login input,
.wrapper_login .login textarea,
.wrapper_login .login textarea::placeholder {
  font-size: 1.2em;
  color: grey !important;
  padding: 10px;
  margin: 5px 0;
  border-radius: 5px;
}

.wrapper_login .login * {
  display: flex;
  flex-direction: column;
  /* margin: 10px 0; */
}
